}

.CardContainer > div > h2,h3,h4,h5,p,span {
    color: #336c64;
}

.CardContainer > div > h1 {
    font-size: 60px;
    font-family: 'Playfair-Black';
    color:#15433D;
}

.servicesContainer{
    margin-top: 20vmin;
    padding-top: 5vmin;
    padding-left: 5%;
