    background-color: #15433D;
}

.DescriptionContainer{
    padding-left: 10%;
}

.serviceCardContainer{
    background-color: white;
    border-radius: 15px;
    padding-top: 5vmin;
    padding-bottom: 10vmin;
    margin-top: 5vmin;
}

.skillIcon{
    height: 10vmin;
}